<template>
    <Interface
        btnLabel="Atualizar" btnIcon="refresh" btnAsEvent noHeader @action="update"
        style="position: relative;"
    >
        <Line style="width: 100%; height: 400px" :data="temps" :options="options" />
    </Interface>
</template>

<script>
import { useDataStore } from '../stores/data.js'
import { Chart as ChartJS, Title, Tooltip, Legend, LineElement, CategoryScale, LinearScale, PointElement, Filler } from 'chart.js'
import { Line } from 'vue-chartjs'
import Interface from '../components/Interface.vue'

ChartJS.register(Title, Tooltip, Legend, LineElement, LinearScale, CategoryScale, PointElement, Filler)

const dateFormat = (date) => {
  try {
    return new Date(new Date(date) - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .replace(/-/g, '/').replace('T', ' - ')
      .replace('Z', '')
      .slice(0, -4)
      // .split(' - ')[1] // just time
  } catch (e) {
    return 'Corrupted date'
  }
}

const options = {
  plugins: {
    legend: {
      display: false
    }
  },
  maintainAspectRatio: false,
  interaction: {
    axis: 'xy',
    mode: 'nearest',
    intersect: false
  },
  scales: {
    x: {
      display: false
    },
    y: {
      display: true
    }
  }
}

export default {
  name: 'Home',
  components: { Line, Interface },
  setup () {
    return { dataStore: useDataStore(), options }
  },
  data: () => ({
    // ...
  }),
  computed: {
    temps () {
      console.log(this.dataStore?.temps)
      return {
        labels: (this.dataStore?.temps || []).map(v => dateFormat(v.date)),
        datasets: [
          {
            label: 'Temperatura',
            data: (this.dataStore?.temps || []).map(v => v.temp),
            borderColor: '#2f7ed8',
            borderWidth: 1,
            pointRadius: 2,
            pointHoverBorderWidth: 10,
            fill: 'start',
            tension: 0.25,
            backgroundColor: (ctx) => {
              const canvas = ctx.chart.ctx
              const gradient = canvas.createLinearGradient(0, -350, 0, 350)

              gradient.addColorStop(0, '#2f7ed8')
              gradient.addColorStop(1, '#171819')

              return gradient
            }
          }
        ]
      }
    }
  },
  methods: {
    update () {
      this.dataStore.loadData(this.$api)
    }
  }
}
</script>
